<template>
	<div id="seniorSettiong-main">
		<h4>高级设置</h4>
		<form>
			<section>
				<label for="">奖品内定：</label>
				<input type="text" name="" placeholder="jack">
			</section>
			<section>
				<label for="">指定抽奖次数：</label>
				<input type="number" name="" placeholder="1">
			</section>
			<section>
				<label for="">奖品每日抽取个数：</label>
				<input type="number" name="" placeholder="10">
			</section>
		</form>
		<section class="submit-btn">保存</section>
	</div>
</template>

<script type="text/javascript">
	export default {
		data(){
			return {}
		},
		mounted(){}
	}
</script>

<style lang="scss" scoped>
	@import '../../../style/mixin';
	#seniorSettiong-main{
		h4{
			@include sc(0.7rem,#929292);
			padding-left:0.5rem;
			height:1.8rem;
			line-height:1.8rem;
		}
		form{
			background:#fff;
			padding:0 0.5rem;
			&::before,&::after{
				display: table;
				content: '';
			}
			section{
				&:last-child{
					margin-bottom:0.5rem;
				}
				label{
					@include sc(0.7rem,#767676);
				}
				input{
					@include sc(0.7rem,#767676);
					&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
						@include sc(0.7rem,#767676);
					}
					&::-moz-placeholder { /* Firefox 19+ */
						@include sc(0.7rem,#767676);
					}
					&:-ms-input-placeholder { /* IE 10+ */
						@include sc(0.7rem,#767676);
					}
					&:-moz-placeholder { /* Firefox 18- */
						@include sc(0.7rem,#767676);
					}
				}
			}
		}
		.submit-btn{
			width: 90%;
			height: 2rem;
			line-height: 2rem;
			background: #f35353;
			text-align: center;
			@include sc(0.7rem,#fff);
			@include borderRadius(0.1rem);
			@include cl;
			bottom:2rem;
		}
	}
</style>


